<template lang="html">
  <div class="home-index">
    <!-- <Row type="flex" justify="space-around" class="code-row-bg statistics">
        <Col span="6">
          <div class="block-Col clearfix">
            <div class="left block-Col-img">
              <img src="../../assets/u1.png" alt="">
            </div>
            <div class="left block-Col-text">
              <h4>1000</h4>
              <p>{{$t("common.New_users")}}</p>
            </div>
          </div>
        </Col>
        <Col span="6">
          <div class="block-Col clearfix">
          <div class="left block-Col-img bg-color">
            <img src="../../assets/u2.png" alt="">
          </div>
          <div class="left block-Col-text">
            <h4>200</h4>
            <p>{{$t("common.New_order")}}</p>
          </div>
        </div>
       </Col>
        <Col span="6">
          <div class="block-Col clearfix">
          <div class="left block-Col-img bg-color2">
            <img src="../../assets/u3.png" alt="">
          </div>
          <div class="left block-Col-text">
            <h4>60</h4>
            <p>{{$t("common.New_products")}}</p>
          </div>
        </div>
       </Col>
        <Col span="6">
          <div class="block-Col clearfix">
          <div class="left block-Col-img bg-color3">
            <img src="../../assets/u4.png" alt="">
          </div>
          <div class="left block-Col-text">
            <h4>1000</h4>
            <p>{{$t("common.Today_revenue")}}</p>
          </div>
        </div>
       </Col>
    </Row> -->
    <Row class="panel-group" :gutter="40">
      <Col :xs="12" :sm="12" :lg="6" class="card-panel-col">
         <div class='card-panel' >
          <div class="card-panel-icon-wrapper icon-people">
            <div class="block-Col-img">
              <img src="../../assets/u1.png" alt="">
            </div>
          </div>
          <div class="card-panel-description">
            <div class="card-panel-num">1000</div>
            <p class="card-panel-text">{{$t("common.New_users")}}</p>
          </div>
        </div>
       </Col>
       <Col :xs="12" :sm="12" :lg="6" class="card-panel-col">
         <div class='card-panel' >
          <div class="card-panel-icon-wrapper icon-message">
            <div class="block-Col-img">
              <img src="../../assets/u2.png" alt="">
            </div>
          </div>
          <div class="card-panel-description">
            <div class="card-panel-num">200</div>
            <p class="card-panel-text">{{$t("common.New_order")}}</p>
          </div>
        </div>
       </Col>
       <Col :xs="12" :sm="12" :lg="6" class="card-panel-col">
         <div class='card-panel' >
          <div class="card-panel-icon-wrapper icon-money">
            <div class="block-Col-img">
              <img src="../../assets/u3.png" alt="">
            </div>
          </div>
          <div class="card-panel-description">
            <div class="card-panel-num">60</div>
            <p class="card-panel-text">{{$t("common.New_products")}}</p>
          </div>
        </div>
       </Col>
       <Col :xs="12" :sm="12" :lg="6" class="card-panel-col">
         <div class='card-panel' >
          <div class="card-panel-icon-wrapper icon-shoppingCard">
            <div class="block-Col-img">
              <img src="../../assets/u4.png" alt="">
            </div>
          </div>
          <div class="card-panel-description">
            <div class="card-panel-num">1000</div>
            <p class="card-panel-text">{{$t("common.Today_revenue")}}</p>
          </div>
        </div>
       </Col>
       
    </Row>
    <Row type="flex" justify="space-around" class="code-row-bg annular">
      <i>{{$t("common.Operational_data_statistics")}}</i>
        <Col span="6">
          <div id="user" :style="{width: '100%', height: '300px',marginTop:'-20px'}"></div>
        </Col>
        <Col span="6">
          <div id="product" :style="{width: '100%', height: '300px',marginTop:'-20px'}"></div>
        </Col>
        <Col span="6">
          <div id="revenue" :style="{width: '100%', height: '300px',marginTop:'-20px'}"></div>
        </Col>
    </Row>
    <Row type="flex" justify="space-around" class="code-row-bg annular">
        <Col span="24">
          <div id="history" :style="{width: '95%', height: '300px',marginTop:'-50px',marginLeft:'20px'}"></div>
        </Col>
    </Row>
    <Row type="flex" justify="space-around" class="code-row-bg activity">
        <Col  span="12" >
          <div id="activity" :style="{width: '90%', height: '350px',marginLeft:'20px'}"></div>
        </Col>
        <Col  span="12" class="border-col">
          <div id="product_statistics" :style="{width: '90%', height: '350px',marginLeft:'20px'}"></div>
        </Col>
    </Row>
  </div>
</template>

<script>
  var echarts = require('echarts');
// // 引入基本模板
// let echarts = require('echarts/lib/echarts')
// // 引入pie图组件
// require('echarts/lib/chart/pie')
// // 引入提示框和title组件
// require('echarts/lib/component/tooltip')
// // 引入line图组件
// require('echarts/lib/chart/line')
// // 引入bar图组件
// require('echarts/lib/chart/bar')
// require('echarts/lib/component/title')
// require('echarts/lib/component/legend')
export default {
  name: 'Dashboard',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted() {
    this.drawLine();

  },
  methods: {
    
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let user = echarts.init(document.getElementById('user'))
      let product = echarts.init(document.getElementById('product'))
      let revenue = echarts.init(document.getElementById('revenue'))
      let history = echarts.init(document.getElementById('history'))
      let activity = echarts.init(document.getElementById('activity'))
      let product_statistics = echarts.init(document.getElementById('product_statistics'))
      // 绘制图表
      var option_product_statistics = {
            title: {
                text: this.$t("common.Product_statistics"),
                x: 'left',
                y: 'top',
                textStyle: { //主标题配置
                  fontWeight: 'normal',
                  fontSize: 16,
                },
                padding:[20,0,0,0]

            },
            tooltip : {
                trigger: 'axis'
            },
            color:['#a4d8cc','#78d6ef'],
            legend: {
               top:'10%',
               right:'5%',
               orient: 'vertical',
               data:[this.$t("common.Personal_product"),this.$t("common.Enterprise_products")]
            },
            xAxis: {
                type: 'category',
                // boundaryGap: [0, 0],
                axisPointer: {
                    snap: true,
                    lineStyle: {
                        color: '#004E52',
                        opacity: 0.5,
                        width: 2
                    },
                    label: {
                        show: true,
                        backgroundColor: '#004E52'
                    }
                },
                axisLine: {
                  show:true,
                  lineStyle:{
                    color:'#C9C9C9'
                  }
                },
                axisTick: {
                  show:false
                },
                axisLabel:{
                  textStyle: {
                      color: '#000'
                  }
                },
                splitLine: {
                    show: false
                },
                data : ['1 '+this.$t("common.Day"),'2 '+this.$t("common.Day"),'3 '+this.$t("common.Day"),'4 '+this.$t("common.Day"),'5 '+this.$t("common.Day"),'6 '+this.$t("common.Day"),'7 '+this.$t("common.Day"),'8 '+this.$t("common.Day")]
            },
            yAxis: {
                type: 'value',
                axisTick: {
                    inside: true,
                    show:false
                },
                axisLine: {
                  show:true,
                  lineStyle:{
                    color:'#C9C9C9'
                  }
                },
                splitLine: {
                    show: false,
                    lineStyle:{
                      opacity:0.3
                    }
                },
                axisLabel: {
                    inside: true,
                    formatter: '{value}\n',
                    align:'right',
                    margin:-10,
                    textStyle: {
                        color: '#000'
                    }
                },
                z: 10,
                max:2000
            },
            grid: {
                top: 90,
                left: 45,
                right: 15,
                height: 200
            },
            series: [
              {
                name: this.$t("common.Personal_product"),
                type: 'bar',
                symbol: 'circle',
                symbolSize: 10,
                sampling: 'average',
                itemStyle: {
                    normal: {
                        color: '#00A4FF'
                    }
                },
                data:[1000,200,600,1500,300,500,800,500]
            },
            {
              name: this.$t("common.Enterprise_products"),
              type: 'bar',
              symbol: 'circle',
              symbolSize: 10,
              sampling: 'average',
              itemStyle: {
                  normal: {
                      color: '#E1EDFA'
                  }
              },
              data:[1300,1050,1200,900,1000,750,780,950]
          }
          ]
        }
      var option_activity = {
            title: {
                text: this.$t("common.Activity_statistics"),
                x: 'left',
                y: 'top',
                textStyle: { //主标题配置
                  fontWeight: 'normal',
                  fontSize: 16,
                },
                padding:[20,0,0,0]

            },
            tooltip : {
                trigger: 'axis'
            },
            color:['#a4d8cc','#78d6ef'],
            legend: {
               top:'10%',
               right:'5%',
               orient: 'vertical',
               data:[this.$t("common.Personal_user"),this.$t("common.Business_users")]
            },
            xAxis: {
                type: 'category',
                // boundaryGap: [0, 0],
                axisPointer: {
                    snap: true,
                    lineStyle: {
                        color: '#004E52',
                        opacity: 0.5,
                        width: 2
                    },
                    label: {
                        show: true,
                        backgroundColor: '#004E52'
                    }
                },
                axisLine: {
                  show:true,
                  lineStyle:{
                    color:'#C9C9C9'
                  }
                },
                axisTick: {
                  show:false
                },
                axisLabel:{
                  textStyle: {
                      color: '#000'
                  }
                },
                splitLine: {
                    show: false
                },
                data : ['1 '+this.$t("common.Day"),'2 '+this.$t("common.Day"),'3 '+this.$t("common.Day"),'4 '+this.$t("common.Day"),'5 '+this.$t("common.Day"),'6 '+this.$t("common.Day"),'7 '+this.$t("common.Day"),'8 '+this.$t("common.Day")]
            },
            yAxis: {
                type: 'value',
                axisTick: {
                    inside: true,
                    show:false
                },
                axisLine: {
                  show:true,
                  lineStyle:{
                    color:'#C9C9C9'
                  }
                },
                splitLine: {
                    show: false,
                    lineStyle:{
                      opacity:0.3
                    }
                },
                axisLabel: {
                    inside: true,
                    formatter: '{value}\n',
                    align:'right',
                    margin:-10,
                    textStyle: {
                        color: '#000'
                    }
                },
                z: 10,
                max:60000
            },
            grid: {
                top: 90,
                left: 45,
                right: 15,
                height: 200
            },
            series: [
              {
                name: this.$t("common.Personal_user"),
                type: 'line',
                symbol: 'circle',
                symbolSize: 10,
                sampling: 'average',
                itemStyle: {
                    normal: {
                        color: '#C4C5C5'
                    }
                },
                data:[31000,18200,32000,29000,30000,57500,37800,20500]
            },
            {
              name: this.$t("common.Business_users"),
              type: 'line',
              symbol: 'circle',
              symbolSize: 10,
              sampling: 'average',
              itemStyle: {
                  normal: {
                      color: '#00CCFF'
                  }
              },
              data:[13000,10500,12000,9000,10000,7500,7800,9500]
          }
          ]
        }
      var option_history = {
            title: {
                text: this.$t("common.Transaction_record"),
                x: 'left',
                y: 'top',
                textStyle: { //主标题配置
                  fontWeight: 'normal',
                  fontSize: 16,
                },
                padding:[60,0,0,10]

            },
            tooltip : {
                trigger: 'axis'
            },
            color:['#a4d8cc','#78d6ef'],
            legend: {
               top:'25%',
               right:'5%',
                data:[this.$t("common.Order_quantity"),this.$t("common.Transaction_amount")]
            },
            xAxis: {
                type: 'category',
                // boundaryGap: [0, 0],
                axisPointer: {
                    snap: true,
                    lineStyle: {
                        color: '#004E52',
                        opacity: 0.5,
                        width: 2
                    },
                    label: {
                        show: true,
                        backgroundColor: '#004E52'
                    }
                },
                axisLine: {
                  show:false
                },
                axisTick: {
                  show:false
                },
                splitLine: {
                    show: false
                },
                data : [this.$t("common.January"),this.$t("common.February"),this.$t("common.March"),this.$t("common.April"),this.$t("common.May"),this.$t("common.June"),this.$t("common.July"),this.$t("common.August")]
            },
            yAxis: {
                type: 'value',
                axisTick: {
                    inside: true,
                    show:false
                },
                axisLine: {
                  show:false
                },
                splitLine: {
                    show: true,
                    lineStyle:{
                      opacity:0.3
                    }
                },
                axisLabel: {
                    inside: true,
                    formatter: '{value}\n',
                    align:'right',
                    margin:20,
                },
                z: 10,
                max:40000
            },
            grid: {
                top: 110,
                left: 15,
                right: 15,
                height: 160
            },
            series: [
              {
                name: this.$t("common.Order_quantity"),
                type: 'line',
                symbol: 'circle',
                symbolSize: [7,5],
                sampling: 'average',
                itemStyle: {
                    normal: {
                        color: '#8ec6ad'
                    }
                },
                areaStyle: {
                    normal: {
                        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                            offset: 0,
                            color: '#2FA1FB'
                        }, {
                            offset: 1,
                            color: '#2FA1FB'
                        }])
                    }
                },
                data:[31000,18200,12000,9000,10000,7500,7800,9500]
            },
            {
              name: this.$t("common.Transaction_amount"),
              type: 'line',
              symbol: 'circle',
              symbolSize: [7,5],
              sampling: 'average',
              itemStyle: {
                  normal: {
                      color: '#8ec6ad'
                  }
              },
              areaStyle: {
                  normal: {
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                          offset: 0,
                          color: '#1CC0AF'
                      }, {
                          offset: 1,
                          color: '#1CC0AF'
                      }])
                  }
              },
              data:[13000,10500,12000,9000,10000,7500,7800,9500]
          }
          ]
        }
      var option_user = {
            title: {
                text: this.$t("common.Total_user"),
                subtext: '100000',
                x: 'center',
                y: 'center',
                textStyle: { //主标题配置
                  fontWeight: 'bold',
                  fontSize: 16,
                },
                subtextStyle: { //子标题配置
                  color: '#39A5F2',
                  fontSize: 16,
                  fontWeight: 'bold',
                },
            },
            tooltip: {},
            series: [{
                name: this.$t("common.User"),
                type: 'pie',
                radius: ['45%', '60%'],
                label: {
                    normal: {
                        show: false,
                    }
                },
                labelLine: {
                  normal: {
                      show: false
                  }
                },
                data: [
                  {value:20000, name:this.$t("common.Business_users"),itemStyle:{normal:{color:'#88C0E3'}}},
                  {value:80000, name:this.$t("common.Personal_user"),itemStyle:{normal:{color:'#1081C5'}}}
                ]
            }]
        }
        var option_product = {
              title: {
                  text: this.$t("common.Online_product"),
                  subtext: '2000',
                  x: 'center',
                  y: 'center',
                  textStyle: { //主标题配置
                    fontWeight: 'bold',
                    fontSize: 16,
                  },
                  subtextStyle: { //子标题配置
                    color: '#F8B44C',
                    fontSize: 16,
                    fontWeight: 'bold',
                  },
              },
              tooltip: {},
              series: [{
                  name: this.$t("common.Product"),
                  type: 'pie',
                  radius: ['45%', '60%'],
                  label: {
                      normal: {
                          show: false,
                      }
                  },
                  labelLine: {
                    normal: {
                        show: false
                    }
                  },
                  data: [
                    {value:1500, name:this.$t("common.Enterprise_products"),itemStyle:{normal:{color:'#FFBD11'}}},
                    {value:500, name:this.$t("common.Personal_product"),itemStyle:{normal:{color:'#FFDE87'}}}
                  ]
              }]
          }
        var option_revenue = {
              title: {
                  text: this.$t("common.Total_revenue"),
                  subtext: '6000000',
                  x: 'center',
                  y: 'center',
                  textStyle: { //主标题配置
                    fontWeight: 'bold',
                    fontSize: 16,
                  },
                  subtextStyle: { //子标题配置
                    color: '#1FBBB3',
                    fontSize: 16,
                    fontWeight: 'bold',
                  },
              },
              tooltip: {},
              series: [{
                  name: this.$t("common.Revenue"),
                  type: 'pie',
                  radius: ['45%', '60%'],
                  label: {
                      normal: {
                          show: false,
                      }
                  },
                  labelLine: {
                    normal: {
                        show: false
                    }
                  },
                  data: [
                    {value:2000000, name:this.$t("common.Amount_paid"),itemStyle:{normal:{color:'#81C9C8'}}},
                    {value:4000000, name:this.$t("common.Other_amount"),itemStyle:{normal:{color:'#009090'}}}
                  ]
              }]
          }
      user.setOption(option_user);
      product.setOption(option_product);
      revenue.setOption(option_revenue);
      history.setOption(option_history);
      activity.setOption(option_activity);
      product_statistics.setOption(option_product_statistics);
      function __resizeHanlder(){
        user.resize();
        product.resize();
        revenue.resize();
        history.resize();
        activity.resize();
        product_statistics.resize();
      }
      setTimeout(function (){
          window.onresize = function () {
            user.resize();
            product.resize();
            revenue.resize();
            history.resize();
            activity.resize();
            product_statistics.resize();
          }
      },200)
      const sidebarElm = document.getElementsByClassName('content-box')[0]
      sidebarElm.addEventListener('transitionend', __resizeHanlder)
    }
  }
}
</script>

<style lang="css">
.home-index{
  text-align: left;
}
.home-index .statistics{
  width: 100%;
  height: 80px;
  
}
.statistics .block-Col{
  width: 100%;
  height: 76px;
  margin-top: 10px;
  margin-left: 30px;

}
.statistics .block-Col-img{
  width: 92px;
  height: 62px;
  background-color: #ccc;
  line-height: 60px;
  border-radius: 5px 0 0 5px;
  text-align: center;
}
.statistics .bg-color{
  background-color: rgba(255, 182, 61, 1);
}
.statistics .bg-color2{
  background-color: rgba(28, 192, 175, 1);
}
.statistics .bg-color3{
  background-color: rgba(251, 106, 0, 1);
}
.block-Col-img img{
  width: 40px;
  height: 40px;
  vertical-align: middle;
}
.statistics .block-Col-text{
  width: 150px;
  height: 62px;
  background-color: #fff;
  border-radius: 0 5px 5px 0;
  line-height: 24px;
  padding-top: 10px;
  padding-left: 10px;
}
.block-Col-text h4{
  font-size: 20px;
}
.home-index .annular{
  width: 100%;
  height: 250px;
  margin-top: 10px;
  position: relative;
  background-color: #fff;
}
.annular i{
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 16px;
}
.home-index .activity{
  width: 100%;
  height: 350px;
  margin-top: 10px;
  position: relative;
  margin-bottom: 50px;
  background-color: #fff;
}

.width-col{
  width: 570px;
}
</style>
<style lang="scss" scoped="" type="text/css">
.panel-group {
  margin-top: 18px;
  .card-panel-col{
    margin-bottom: 32px;
  }
  .card-panel {
    height: 108px;
    cursor: pointer;
    font-size: 12px;
    position: relative;
    overflow: hidden;
    color: #666;
    background: #fff;
    box-shadow: 4px 4px 40px rgba(0, 0, 0, .05);
    border-color: rgba(0, 0, 0, .05);
    
    .card-panel-icon-wrapper {
      color: #fff;
    }
    .icon-people {
       background: #40c9c6;
    }
    .icon-message {
      background: #36a3f7;
    }
    .icon-money {
      background: #f4516c;
    }
    .icon-shoppingCard {
      background: #FFB63D;
    }
    .icon-people {
      color: #40c9c6;
    }
    .icon-message {
      color: #36a3f7;
    }
    .icon-money {
      color: #f4516c;
    }
    .icon-shoppingCard {
      color: #34bfa3;
    }
    .card-panel-icon-wrapper {
      float: left;
      margin: 14px 0 0 14px;
      padding: 16px;
      transition: all 0.38s ease-out;
      border-radius: 6px;
    }
    .card-panel-icon {
      float: left;
      font-size: 48px;
    }
    .card-panel-description {
      float: right;
      font-weight: bold;
      margin: 26px;
      margin-left: 0px;
      .card-panel-text {
        line-height: 18px;
        color: rgba(0, 0, 0, 0.45);
        font-size: 16px;
        margin-bottom: 12px;
      }
      .card-panel-num {
        font-size: 20px;
      }
    }
  }
}
</style>



